import { NextResponse } from "next/server";

export const config = {
  api: {
    bodyParser: false, // Disable body parsing to handle FormData
  },
};

export default async function handler(req, res) {
  console.log(req.method)
  if (req.method !== "POST") {
    res.status(405).json({ error: "Method not allowed" });
    return;
  }

  try {
    const backendUrl = "http://localhost:8080/upload-file-s3"; // Go backend URL

    // Forward the request body to the Go backend
    const response = await fetch(backendUrl, {
      method: "POST",
      headers: {
        ...req.headers,
      },
      body: req,
    });

    if (!response.ok) {
      const error = await response.text();
      res.status(response.status).json({ error: error });
      return;
    }

    const data = await response.json();
    res.status(200).json(data);
  } catch (error) {
    console.error("Error in API route:", error);
    res.status(500).json({ error: "Internal server error" });
  }
}
